import React from "react";
import {
  compose,
  withStateHandlers,
  StateHandler,
  StateHandlerMap,
} from "recompose";
import arrowUp from "assets/img/icons/orange/arrow-up.svg";
import arrowDown from "assets/img/icons/orange/arrow-down.svg";
import {
  FaqHeader,
  FaqIcon,
  FaqQuestion,
  FaqResponseOpen,
  FaqWrapper,
} from "./elements";

interface FAQItemExternalProps {
  answer: JSX.Element;
  name?: string; // don't see this actually being passed in
  question: string;
  location: Location;
}

interface FAQItemStateProps {
  isOpen: boolean;
}

type FAQItemStateHandlerProps = StateHandlerMap<FAQItemStateProps> & {
  toggle: StateHandler<FAQItemStateProps>;
};

type FAQItemProps = FAQItemExternalProps &
  FAQItemStateProps &
  FAQItemStateHandlerProps;

const FAQItem: React.SFC<FAQItemProps> = ({
  answer,
  question,
  isOpen,
  toggle,
}) => (
  <FaqWrapper>
    <FaqHeader onClick={toggle}>
      <FaqQuestion>{question}</FaqQuestion>
      <FaqIcon src={!isOpen ? arrowDown : arrowUp} />
    </FaqHeader>
    {isOpen && <FaqResponseOpen>{answer}</FaqResponseOpen>}
  </FaqWrapper>
);

export default compose<FAQItemProps, FAQItemExternalProps>(
  withStateHandlers<FAQItemStateProps, FAQItemStateHandlerProps, FAQItemProps>(
    ({ name, location }) => ({
      isOpen: name ? location.hash === `#${name}` : false,
    }),
    {
      toggle: ({ isOpen }) => () => ({
        isOpen: !isOpen,
      }),
    },
  ),
)(FAQItem);
